import React from 'react'
import {NavLink} from 'react-router-dom';
import Social from './Social';
import brand from './components/brand.jpg'

function Navbar() {
  return <nav className="navbar sticky navbar-expand-lg">
    <div className="container-fluid">
      <div className="navbar-brand">
      <img src={brand} className='rounded-circle' alt='brand'/>
      <span className='danger'> Akvarium olami</span></div>
      <button className="navbar-toggler border-0" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="fa-solid fa-bars-staggered affect"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav m-auto mb-2 mb-lg-0">
          <li className="nav-item">
          <NavLink className={'nav-link'} to={'/home'}><i className="fa-solid fa-house"></i></NavLink>
          </li>
        </ul>
       <Social/>
      </div>
    </div>
  </nav>
}

export default Navbar