import React from 'react'
import Btn1 from './Btn1';

function Card({a}) {

  return <div className="col-xs-12 col-sm-6 col-md-3 col-lg-3" >
  <div className="card-flyer">
      <div className="text-box">
          <div className="image-box">
              <img src={a.img} alt={a.mdl} />
          </div>
          <div className="text-container">
              <h6>{a.mdl}</h6>
              <p>{a.des}</p>
              <div className='row'>
                  <div className='price col-6'>{a.prc - (a.prc/100) * a.disc}$ / <span className='not-price'>{a.prc}$</span></div>
                  <div className='data col-6'>{a.dt}</div>
              </div>
              <Btn1 prop={a}/>
          </div>
          <div className='type'><span className='premium'>{a.dtl}</span></div>
      </div>
  </div>
</div>
}

export default Card