import React from 'react'
import Title from './Title'

function About() {

  return <div className='row mt-5 mb-5'>
        <Title prop={{name: "Biz Haqimizda", text: "Biz haqimizda bilib oling !"}}/>
        <div className='col-md-6 col-sm-12 mb-3'>
            <img src='https://www.aquariumarchitecture.com/site/wp-content/uploads/2015/03/aquarium-architecture-luxury-london-townhouse-hampstead-aquarium-design-2014-26.jpg' className='about-img card-top-img' alt='about'/>
            <p className='about-text'>Bu faoiliyatimiz davomidagi eng kamyob dizayn.</p>
        </div>
        <div className='col-md-6 col-sm-12 mb-3'>
            <div className='about'>
                <div className='line'></div>
                <p>Bizning kompaniyamiz 2001/2002 yilda, Toshkent Samarqand Darvozada tashkil etilgan. Kompaniyamiz Hasan va Husanlar tomonidan asos solingan. Boshlang'ich maqsadimiz O'zbekistonda boshqa davlatlarga qaraganda akvarium sohasini rivojlantirish edi va bizning missiyamiz akvarium ishlab chiqarish hisoblanadi. Ilk mahsulotimiz Classic dizayndagi akvariumlar edi va u juda tez ommalashgan. O'tgan yil davomida biz 624ta katta hajmli akvariumlarni mijozlarga yetkazib berdishga erishdik. Kompaniyamizning asosiy qadriyatlari bo'lib, biz jamoamizni asosiy madaniy jihatlar bilan qo'llab-quvvatlaymiz. Kelajakda biz online savdo va yangi dizaynlar taqdim etishda davom etamiz.</p>
                <button className='btn btn-primary w-100'><a href='https://www.instagram.com/akvarium_olami' className='nav-link'>Ko'proq</a></button>
            </div>
        </div>
    </div>
}

export default About