import React, { useEffect, useState } from 'react'
import Way from './config';
import Title from './Title'

function Gallery() {

  const [des, Setdes] = useState([]);

  useEffect(() => {
    Way.get("design.json")
      .then(res => Setdes(res.data));
  }, [])

  return <div className='row mt-5 mb-5 gallery-row'>
    <Title prop={{name: "Gallereya", text: "Bu yerda siz xohlagan akvarium dizaynlari bor."}}/>
    {
      des.map((a, b) => <div key={b} className='col-md-2 col-sm-12 mb-3'>
        <div className='card des'>
          <img src={`https://raw.githubusercontent.com/HelloAbdulloh/br1img/main/${a}.jpg`} className='card-top-img' alt='#' />
        </div>
      </div>)
    }
  </div>
}
export default Gallery