import { createContext, useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import Footer from "./Footer";
import Navbar from "./Navbar";
import Router from "./Router"

export const Mycontext = createContext();

function App() {

    const navigate = useNavigate();
    const [timeline, setTimeline] = useState(false);
    const [page, setPage] = useState(1);

    useEffect(() => {
        setTimeline(false)
        navigate('/');
        setTimeout(() => {
            page ? allowedPage() : navigate('/error');
        }, 5200);

        function allowedPage(){
                setTimeline(true);
                navigate('/home');
        }
    }, []);

    function Submit(e) {
        e.preventDefault();
        let sunc = `${e.target[1].value} ${e.target[0].value} ${e.target[2].value}`
        var url = `https://api.telegram.org/bot6509606080:AAEzTucp2ZAIvIHH1PIQRZeDwngN2Fz5rDY/sendMessage?chat_id=-1001591961491&text=${sunc}`
        let api = new XMLHttpRequest();
        api.open("GET", url, true);
        api.send();
        navigate('/home');
        setTimeline(true);
        e.target[0].value = null
        e.target[1].value = null
        e.target[2].value = null
        alert('Arizangiz qabul qilindi, javobni kuting !')
    }

    return <>
        <div className='box'>
            <div className="area" >
                <ul className="circles">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>

                </ul>
            </div >
        </div>
        {timeline ? <Navbar /> : ''}
        <Mycontext.Provider value={{
            Submit, setTimeline
        }}>
            <Router />
        </Mycontext.Provider>
        {timeline ? <Footer/> : ''}
    </>
}
export default App