import React, { useEffect, useState } from 'react'
import Way from './config';
import Card from './Card';
import Title from './Title'

function ForSale() {

    const [sales, setSales] = useState([]);
    
    useEffect(() => {
        Way.get("sale.json")
            .then(res => setSales(res.data));
    },[]);

    return <div id='cards_landscape_wrap-2'>
            <div className='row mt-5 mb-5'>
                <Title prop={{name: "Hafta chegirmasi%", text: "Bizning mahsulotlarimiz sizning hayotingizni yanada yorqinroq va qulayroq qilish uchun mo'ljallangan."}}/>
               {
                sales.map((a,b)=> <Card key={b} a={a}/>)
               }
        </div>
    </div>
}

export default ForSale