import React, { useEffect, useState } from 'react'
import Way from './config';
import Title from './Title'

function Slider() {

    const [slide, Slider] = useState([]);
    useEffect(() => {
        Way.get("brands.json")
            .then(res => Slider(res.data));
    }, [])

    return <div className='row mt-5 mb-5'>
        <Title prop={{name: "Takliflar",text: "Har bir mijoz biz uchun muhim, shuning uchun sizga eng yaxshi xizmatni taklif qilamiz. "}}/>
        <div className="slider slidr">
            <div className="slide-track">
                {
                    slide.map((det,b) => <div className="slide" key={b}>
                    <img src={det} height="100" width="250"/>
                </div>)
                }
            </div>
        </div>
    </div>
}

export default Slider