import React from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import Title from './Title'

function OpenCard() {

  const navigate = useNavigate();
  const {state} = useLocation();

  return <>
  <div className='container pt-5'>
    <div className='row mb-5 mt-5'>
      <Title prop={{name: `Mahsulot: ${state?.mdl}`}}/>
      <div className='col-md-6 col-sm-12 open-carusel'>
        <img src={state?.img} className={'w-100'} alt='#'/>
      </div>
      <div className='col-md-6 col-sm-12'>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">First</th>
              <th scope="col">Last</th>
              <th scope="col">Handle</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row">1</th>
              <td colSpan="3">{state?.mdl}</td>
            </tr>
            <tr>
              <th scope="row">2</th>
              <td>Razmer</td>
              <td>x,y,z</td>
              <td>{`${state?.dsn[0]}x${state?.dsn[1]}x${state?.dsn[2]}`}</td>
            </tr>
            <tr>
              <th scope="row">3</th>
              <td>Rangi</td>
              <td> {state?.clr}</td>
              <td>yo'gochli</td>
            </tr>
            <tr>
              <th scope="row">4</th>
              <td>Oyna</td>
              <td>{state?.pdf[0]}</td>
              <td>{state?.pdf[1]}</td>
            </tr>
            <tr>
              <th scope="row">5</th>
              <td>Narxi</td>
              <td>Shu holatda {state?.prc}$</td>
              <td>dastavka 7$</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div className='col-md-12 mt-5'>
        <button className='btn btn-primary w-100' onClick={()=> navigate('/home')}>Yana boshqa madel.</button>
      </div>
    </div>
  </div>
  </>
}

export default OpenCard