import React, { useEffect, useState } from 'react'
import Way from './config';
import Card from './Card'
import Title from './Title'

function Projects() {

  const [thisPage, setThisPage] = useState(0);
  const [products, setProducts] = useState([]);

  useEffect(() => {
    Way.get('pro.json')
      .then(res => setProducts(eval(res.data)))
      .catch(r => r)
  }, []);

  return <div id="cards_landscape_wrap-2">
    <div className="row mb-5 mt-5" data-aos="fade-up">
      <Title prop={{ name: "Yangi loyihalar", text: "Hozirgi kundagi eng ommabop madellar to'plami !" }} />
      {
        products[thisPage]?.map(a => <Card key={a.id} a={a} />)
      }
      <div className='col-12'>
        <div className='row text-center mt-5 mb-5'>
          <div className='col-4'><button className='btn btn-primary w-100' onClick={() => setThisPage(prev => thisPage >= 1 ? prev - 1 : prev)}>Orqaga</button></div>
          <div className='col-4 fs-4 about'>{thisPage + 1}</div>
          <div className='col-4'><button className='btn btn-primary w-100' onClick={() => setThisPage(prev => thisPage < products.length - 1 ? prev + 1 : prev)}>Oldinga</button></div>
        </div>
      </div>
    </div>
  </div>

}

export default Projects