import React, { useEffect, useState } from 'react'

function Carusel() {

    let alfa = [
        {
            "title": "Bizning narxlarimiz hamyonbop, sifati esa yuqori.",
            "text": "Biz bilan ishlash – bu sifati va ishonchliligi kafolatlangan tanlov."
        }, {
            "title": "Bizning mahsulotlarimiz har doim sifati bilan ajralib turadi, sifat bor joyda ishonch ham bo'ladi.",
            "text": "Sizning ehtiyojlaringiz biz uchun muhim, shuning uchun faqat eng yaxshisini taklif qilamiz."
        }, {
            "title": "Bizning xizmatlarimiz hayotingizni osonlashtirish uchun mo'ljallangan.",
            "text": "Mahsulotlarimizning har biri yuqori sifat nazoratidan o'tkaziladi."
        }
    ]

    const [count, setCount] = useState(0);
    useEffect(() => {
        setInterval(() => {
            setCount((Math.random() * 3).toFixed());
        }, 10000);
    }, [])
    return <div className='d-flex justify-content-center align-items-center w-100 vh-100 home1'>
        <div>
            <h1>{alfa[count]?.title}</h1>
            <p>{alfa[count]?.text}</p>
        </div>
    </div>
}

export default Carusel